<template>
  <div class="register_layout">
    <div class="register_steps">
      <el-steps
        :space="400"
        :active="step"
        finish-status="success"
        align-center
      >
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
      </el-steps>
    </div>
    <div class="set_telephone_number" ref="setTelephoneNumber">
      <el-form
        :model="registerForm"
        label-width="80px"
        label-position="right"
        status-icon
        :rules="rules"
        ref="registerForm"
      >
        <!--  -->
        <el-form-item label="" prop="telNumber">
          <el-input
            type="text"
            clearable
            v-model="registerForm.telNumber"
            placeholder=""
            autocomplete="off"
            maxlength="11"
          ></el-input>
        </el-form-item>
        <!--  -->
        <el-form-item label="" prop="verificationCode">
          <el-input
            type="text"
            v-model="registerForm.verificationCode"
            placeholder="6"
            autocomplete="off"
          >
            <el-button slot="suffix" @click="generateCode()"
              ></el-button
            >
          </el-input>
        </el-form-item>

        <div class="register_agree_policy">
          <a></a><a></a>
        </div>

        <el-form-item>
          <el-button type="primary" @click="nextStep()"
            ></el-button
          >
        </el-form-item>
      </el-form>
    </div>
    <div class="set_passwd" ref="setPasswd">
      <el-form
        :model="registerForm"
        label-width="80px"
        label-position="right"
        status-icon
        :rules="rules"
        ref="registerForm"
      >
        <!--  -->
        <el-form-item label="" prop="accounter">
          <el-input
            type="text"
            clearable
            v-model="registerForm.accounter"
            autocomplete="off"
            placeholder="515"
            maxlength="15"
          ></el-input>
        </el-form-item>
        <!--  -->
        <el-form-item label="" prop="email">
          <el-input
            type="email"
            clearable
            v-model="registerForm.email"
            autocomplete="off"
            placeholder=""
          ></el-input>
        </el-form-item>
        <!--  -->
        <el-form-item label="" prop="password">
          <el-input
            type="password"
            clearable
            v-model="registerForm.password"
            autocomplete="off"
            show-password
            placeholder="8"
          ></el-input>
        </el-form-item>
        <el-form-item label="" prop="checkpass">
          <el-input
            type="password"
            v-model="registerForm.checkpass"
            placeholder=""
            show-password
            autocomplete="off"
          ></el-input>
        </el-form-item>

        <el-form-item>
          <el-button type="primary" @click="nextStep(registerForm)"
            >&nbsp;&nbsp;&nbsp;</el-button
          >
        </el-form-item>
      </el-form>
    </div>
    <div class="register_completed" ref="registerComplete">
      <el-result icon="success" title="" subTitle="">
        <template slot="extra">
          <div>
            <a href="#" @click="toNextPage('/login')"></a>
          </div>
        </template>
      </el-result>
    </div>
  </div>
</template>

<script>
export default {
  name: "RegisterComponent",
  data() {
    // 
    const validateTel = (rule, value, callback) => {
      const regex = /1[3-9][0-9]{9}/g;
      if (value === "") {
        callback(new Error(""));
      } else {
        if (!regex.test(value)) {
          callback(new Error(", "));
        } else {
          callback();
        }
      }
    };

    // 
    const validateCode = (rule, value, callback) => {
      if (value === "") {
        callback(new Error(""));
      } else {
        callback();
      }
    };

    //
    const validateAccounter = (rule, value, callback) => {
      if (value === "") {
        callback(new Error(""));
      } else {
        const regex = /[A-z]+[\w]{4,14}/g;
        if (!regex.test(value) || value.length < 5 || value.length > 15) {
          callback(new Error("515"));
        } else {
          callback();
        }
      }
    };

    // 
    const validateEmail = (rule, value, callback) => {
      if (value === "") {
        callback(new Error(""));
      } else {
        const regex = /[\w]+@[A-z]+(\.[A-z]+){1,2}/g;
        if (!regex.test(value)) {
          callback(new Error(""));
        } else {
          callback();
        }
      }
    };

    // 
    const validatePass = (rule, value, callback) => {
      if (value === "") {
        callback(new Error(""));
      } else {
        if (
          !(
            /[a-z]+/.test(value) &&
            /[A-Z]+/.test(value) &&
            /[\w]{8,}/.test(value)
          )
        ) {
          callback(new Error("8"));
        } else if (this.registerForm.checkpass !== "") {
          this.$refs.setPass.validateField("checkpass");
        } else {
          callback();
        }
      }
    };
    const validatePass2 = (rule, value, callback) => {
      if (value === "") {
        callback(new Error(""));
      } else {
        if (
          !(
            /[a-z]+/.test(value) &&
            /[A-Z]+/.test(value) &&
            /[\w]{8,}/.test(value)
          )
        ) {
          callback(new Error("8"));
        } else if (value !== this.registerForm.password) {
          callback(new Error("!"));
        } else {
          callback();
        }
      }
    };
    return {
      step: 1,
      registerForm: {
        telNumber: "",
        verificationCode: "",
        accounter: "",
        email: "",
        password: "",
        checkpass: "",
      },
      rules: {
        telNumber: [{ validator: validateTel, trigger: "blur" }],
        verificationCode: [{ validator: validateCode, trigger: "blur" }],
        accounter: [{ validator: validateAccounter, trigger: "blur" }],
        email: [{ validator: validateEmail, trigger: "blur" }],
        password: [{ validator: validatePass, trigger: "blur" }],
        checkpass: [{ validator: validatePass2, trigger: "blur" }],
      },
      randomCode: "code",
    };
  },
  methods: {
    generateCode() {
      let code = "";
      for (let i = 0; i < 6; i++) {
        code += parseInt(Math.random() * 10).toString();
      }
      this.randomCode = code;
      this.$alert(this.randomCode, "", {
        confirmButtonText: "",
        center: true,
      });
    },
    async nextStep(registerForm) {
      // ->
      if (this.step == 1) {
        // 
        if (this.registerForm.verificationCode !== this.randomCode) {
          this.$message({
            showClose: true,
            message: "",
            type: "error",
          });
        } else {
          // 
          this.$refs.setTelephoneNumber.style.display = "none";
          this.$refs.setPasswd.style.display = "block";
          this.step++;
        }
      }
      // ->
      else if (this.step == 2) {
        // 
        if (this.registerForm.password != this.registerForm.checkpass) {
          this.$message({
            showClose: true,
            message: "",
            type: "error",
          });
        } else {
          // 
          let res = await this.register({
            accounter: registerForm.accounter,
            password: registerForm.password,
            telephone: registerForm.telNumber,
            email: registerForm.email,
          });
          if (res.code == 0 && res.msg == "") {
            this.$refs.setPasswd.style.display = "none";
            this.$refs.registerComplete.style.display = "block";
            this.step++;
          } else {
            let msg = "";
            if (res.msg) {
              msg = res.msg;
            }
            this.$message({
              showClose: true,
              message: msg,
              type: "error",
            });
          }
        }
      }
    },
    register(body) {
      return this.$post("/register", body);
    },
    toNextPage(path) {
      this.$router.push(path);
    },
  },
  deactivated() {
    this.step = 1;
    this.registerForm.telNumber = "";
    this.registerForm.verificationCode = "";
    this.registerForm.accounter = "";
    this.registerForm.email = "";
    this.registerForm.password = "";
    this.registerForm.checkpass = "";
  },
};
</script>

